import "core-js/modules/es.array.push.js";
import "core-js/modules/es.set.difference.v2.js";
import "core-js/modules/es.set.intersection.v2.js";
import "core-js/modules/es.set.is-disjoint-from.v2.js";
import "core-js/modules/es.set.is-subset-of.v2.js";
import "core-js/modules/es.set.is-superset-of.v2.js";
import "core-js/modules/es.set.symmetric-difference.v2.js";
import "core-js/modules/es.set.union.v2.js";
import { mapGetters } from 'vuex';
export default {
  name: 'CustomerConfig',
  data() {
    return {
      loading: false,
      tableData: [],
      dealedData: [] // 根据IP地址处理后的数据，格式为 {ip, count, [data]}
    };
  },
  computed: {
    ...mapGetters(['credentials']),
    disableTooltip() {
      return window.innerWidth < 768;
    }
  },
  methods: {
    handlePageChange(page, row) {
      row.page = page;
      this.getList(row);
    },
    handlePageSizeChange(pageSize, row) {
      row.pageSize = pageSize;
      this.getList(row);
    },
    getList(row) {
      if (!row.expand) {
        this.fetchWithAuth("/api/manage/list?ip=" + row.UploadIP, {
          method: 'GET'
        }).then(response => response.json()).then(result => {
          row.data = this.dealByIP(result.data); // 根据IP地址处理数据
          row.expand = true;
          this.loading = false;
        }).catch(err => {
          this.loading = false;
          if (err.message !== 'Unauthorized') {
            this.$message.error('同步数据时出错，请检查网络连接');
          }
        });
      }
    },
    expandRow(row, expandedRows) {
      this.loading = true;
      this.getList(row);
    },
    async fetchWithAuth(url, options = {}) {
      // 开发环境, url 前面加上 /api
      // url = `/api${url}`;
      if (this.credentials) {
        // 设置 Authorization 头
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // 确保包含凭据，如 cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    dealByIP(data) {
      let dealedData = [];
      let ipSet = new Set();
      data.records.forEach(item => {
        if (item?.UploadIP) {
          ipSet.add(item.UploadIP);
        }
      });
      ipSet.forEach(ip => {
        let ipData = data.records.filter(item => item?.UploadIP === ip);
        let count = data.total;
        dealedData.push({
          ip,
          count,
          total: count,
          page: 1,
          pageSize: 15,
          disabledPage: false,
          UploadIP,
          data: []
        });
      });
      return dealedData;
    },
    handleGoUpload() {
      this.$router.push('/');
    },
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    handleGoHome() {
      this.$router.push('/dashboard');
    },
    formatTimeStamp(timeStamp) {
      return new Date(timeStamp).toLocaleString();
    },
    sortByTimestamp(a, b) {
      return new Date(a.metadata.TimeStamp) - new Date(b.metadata.TimeStamp);
    }
  },
  mounted() {
    this.fetchWithAuth("/api/manage/check", {
      method: 'GET'
    }).then(response => response.text()).then(result => {
      if (result == "true") {
        this.showLogoutButton = true;
        // 在 check 成功后再执行 list 的 fetch 请求
        return fetch("https://studyjava.cn/api/cloudflare/files/ip", {
          method: 'GET'
        });
      } else if (result == "Not using basic auth.") {
        return fetch("https://studyjava.cn/api/cloudflare/files/ip", {
          method: 'GET'
        });
      } else {
        throw new Error('Unauthorized');
      }
    }).then(response => response.json()).then(result => {
      // this.tableData = result;
      this.tableData = [];
      this.dealedData = this.dealByIP(result.data); // 根据IP地址处理数据
    }).catch(err => {
      if (err.message !== 'Unauthorized') {
        this.$message.error('同步数据时出错，请检查网络连接' + err.message);
      }
    });
  }
};